let existingIframe = null

function openPopupInIframe(url, title) {
  // if (window !== window.top) {
  //     // If we are in an iframe, use the parent window to control the existing iframe
  //     parent.openPopupInIframe(url, title);
  //     return;
  // }
  if (!existingIframe) {
    const container = document.createElement('div') // Create a container div
    container.style.position = 'fixed'
    container.style.bottom = '-100%' // Start with the container below the viewport
    container.style.left = '0'
    container.style.width = '100%'
    container.style.backgroundColor = 'rgba(0, 0, 0, 0.9)'
    container.style.height = '100%'
    container.style.zIndex = '9999'
    container.style.transition = 'bottom 0.5s' // Add a transition for the 'bottom' property
    document.body.appendChild(container)

    const iframe = document.createElement('iframe')
    iframe.src = url
    iframe.title = title
    iframe.id = 'launchGame_iframe'
    iframe.style.position = 'fixed'
    iframe.style.width = '100%'
    iframe.style.height = `94%`
    iframe.style.border = 'none'
    iframe.style.bottom = '0'
    iframe.style.left = '0'

    container.appendChild(iframe) // Append iframe to the container

    const closeButton = document.createElement('button')
    closeButton.textContent = 'X' // Button text
    closeButton.style.position = 'absolute'
    closeButton.style.color = '#fff'
    closeButton.style.backgroundColor = 'rgba(0, 0, 0, 0.25)'
    closeButton.style.top = '0'
    closeButton.style.right = '0'
    closeButton.style.height = '6%'
    closeButton.style.width = '50px'
    closeButton.style.zIndex = '10000'
    closeButton.addEventListener('click', () => {
      container.style.bottom = '-100%' // Slide out the container when the close button is clicked
      setTimeout(() => {
        document.body.removeChild(container) // Remove the container after the animation is complete
      }, 100)
      // Reset the existingIframe variable to null
      existingIframe = null
    })

    container.appendChild(closeButton) // Append close button to the container

    const image = document.createElement('img')
    image.src = '/static/image/home/logo.webp' // Replace with the actual path to your image
    image.style.position = 'absolute'
    image.style.top = '0' // Adjust top position as needed
    image.style.left = '10px' // Adjust left position as needed
    image.style.width = '115px'
    image.style.padding = '10px'
    image.style.zIndex = '10000' // Higher z-index to be above the iframe

    container.appendChild(image) // Append image to the container

    // Trigger the animation by setting the 'bottom' property to 0 after a slight delay
    // setTimeout(() => {
    //     container.style.bottom = '0';
    // }, 100);
    document.body.appendChild(container)
    setTimeout(() => {
      container.style.bottom = '0'
    }, 100)
  } else {
    existingIframe.src = url // Reuse the existing iframe by updating its src
    const container = existingIframe.parentNode
    container.style.bottom = '0' // Ensure the container is visible
  }
}

function openPopup(url, title, popupWidth, popupHeight) {
  let isAndroidMobile = localStorage.getItem(CACHE_KEY.IS_ANDROID)
  if (isAndroidMobile != null) {
    isAndroidMobile = JSON.parse(isAndroidMobile)
  }
  if (isAndroidMobile != null && isAndroidMobile.value) {
    const dualScreenLeft = window.screenLeft !== undefined ? window.screenLeft : window.screenX
    const dualScreenTop = window.screenTop !== undefined ? window.screenTop : window.screenY

    const width = window.innerWidth ? window.innerWidth : document.documentElement.clientWidth ? document.documentElement.clientWidth : screen.width
    const height = window.innerHeight ? window.innerHeight : document.documentElement.clientHeight ? document.documentElement.clientHeight : screen.height

    const systemZoom = width / window.screen.availWidth
    const left = (width - popupWidth) / 2 / systemZoom + dualScreenLeft
    const top = (height - popupHeight) / 2 / systemZoom + dualScreenTop
    const newWindow = window.open(
      '',
      title,
      `
            width=${popupWidth}, 
            height=${popupHeight}, 
            top=${top}, 
            left=${left}
            `
    )

    newWindow.document.write('Redirecting...')
    newWindow.location.href = url
  } else {
    window.open(url, '_blank')
  }

  // if (window.hasOwnProperty('focus')) newWindow.focus()
}

